/* -----------------------------------------------------------------------------
 This file contains all styles related to the footer of the site/application.
----------------------------------------------------------------------------- */
.footer {
  padding: 0px 0;
  margin-top: 0;
  background: #fff;
  border-top: 8px solid #DA291C;
  position: fixed;
  width: 100%;
  left: 0;
  bottom: 0;
  z-index: 99;
  padding-top: 16px;
  padding-bottom: 5px; }

.footer {
  padding: 30px 0;
  margin-top: 20px;
  background: #fff;
  border-top: 8px solid #DA291C; }
  .footer .footer-links {
    max-width: 90%;
    margin: 0 auto;
    color: #A2A2A4;
    font-size: 14px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* // ul {
        //     margin: 0;
        //     li {
        //         display: inline-block;
        //         list-style: none;
        //         a {
        //             color: #A2A2A4;;
        //             padding: 0 15px;
        //             border-right: 1px solid #A2A2A4;;
        //             &:hover {
        //                 color: #A2A2A4;;
        //             }
        //         }
        //         &:last-child {
        //             a {
        //                 border: 0;
        //             }
        //         }
        //     }
        // } */ }
    .footer .footer-links div {
      flex: 1;
      line-height: 30px; }
    .footer .footer-links .links p {
      display: inline-block;
      list-style: none; }
      .footer .footer-links .links p a {
        color: #A2A2A4;
        padding: 0 15px;
        border-right: 1px solid #A2A2A4; }
        .footer .footer-links .links p a:hover {
          color: #DA291C;
          text-decoration: none; }
      .footer .footer-links .links p:last-child a {
        border: 0; }
    .footer .footer-links p {
      font-size: 14px; }
    .footer .footer-links .logo {
      background: url(../../cs/cop/assets/images/avaya-logo-red.svg) no-repeat bottom left;
      height: 30px;
      width: 200px; }
    .footer .footer-links .links {
      flex: 2;
      text-align: center; }
    .footer .footer-links .copyright {
      text-align: right; }
    .footer .footer-links a.optanon-alert-box-close {
      display: none; }
    .footer .footer-links div.optanon-show-settings-left {
      display: none; }
    .footer .footer-links div.optanon-show-settings-right {
      display: none; }
    .footer .footer-links div.optanon-show-settings-popup {
      display: none !important;
      left: -9999px; }
    .footer .footer-links #optanon-show-settings-popup {
      display: none !important; }
    .footer .footer-links div.optanon-show-settings-popup-wrapper {
      display: inline-block; }
    .footer .footer-links div.optanon-show-settings-popup-wrapper div.optanon-show-settings-middle {
      border: 0;
      background: 0 0 !important;
      float: none;
      height: auto !important;
      padding: 0; }
    .footer .footer-links div.optanon-show-settings-popup-wrapper div.optanon-show-settings-middle a.optanon-show-settings {
      color: #A2A2A4 !important;
      font-weight: 300;
      margin-top: 0 !important;
      float: none !important;
      font-size: 100%;
      font-size: 14px;
      font-family: "Proxima Nova", proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif !important; }
    .footer .footer-links div.optanon-show-settings-popup-wrapper div.optanon-show-settings-middle a.optanon-show-settings:hover {
      color: #DA291C; }
    .footer .footer-links .optanon-show-settings:hover {
      color: #DA291C !important; }
    .footer .footer-links a:hover {
      color: #DA291C !important; }
    .footer .footer-links a.optanon-show-settings {
      cursor: pointer; }
  @media screen and (max-width: 767px) {
    .footer {
      padding: 30px 20px; }
      .footer .footer-links {
        display: block;
        text-align: left; }
        .footer .footer-links .links {
          margin-left: -10px;
          text-align: left; }
          .footer .footer-links .links ul li {
            padding: 5px 0; }
            .footer .footer-links .links ul li a {
              padding: 0 7px; }
        .footer .footer-links .copyright {
          text-align: left; } }

